import React, { Component } from 'react';
import Form from './Form';
import Tasks from './Tasks';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      items: [],
      currentItems: {
        text: '',
        key: ''
      }

    }
  }

  render() {
    return (
      <div className="wrap">
        <div className="container">
          <Form style={{ width: "100%" }} handleChange={this.handleChange} handleSubmit={this.handleSubmit} text={this.state.currentItems.text} />
        </div>
        <div className="container">
          <Tasks items={this.state.items} deleteItem={this.deleteItem} changeText={this.changeText} />
        </div>
      </div>
    );
  }

  handleChange = (event) => {
    this.setState({
      currentItems: {
        text: event.target.value,
        key: Date.now()
      }
    })
  }

  changeText = (text, index) => {
    let items = this.state.items;
    this.setState({
      items: items.map((item, i) => {
        if (index === i) {
          item.text = text;
        }
        return items;
      }),
    })
  }

  handleSubmit = (event) => {
    event.preventDefault();
    if (this.state.currentItems.text !== "") {
      const items = [...this.state.items, this.state.currentItems];
      this.setState({
        items: items,
        currentItems: {
          text: '',
          key: ''
        }
      })
    }

  }

  deleteItem = (index) => {
    this.setState({
      items: this.state.items.filter((item, i) => {
        return i !== index;
      }),
    })
  }

}

export default App;
