import React, { Component } from 'react';
class Form extends Component {

    render() {
        return (
            <form style={{ width: "100%" }} id="form-inline" className="form-inline mt-5 w-100" onSubmit={this.props.handleSubmit}>
                <input onChange={this.props.handleChange} id="text"
                    value={this.props.text} name="text" className="form-control" 
                    onFocus={(e) => e.target.placeholder = ""}
                    onBlur={(e) => e.target.placeholder = "Add new task"}
                    placeholder="Add new task" />
                <button id="add" type="submit" className="btn btn-primary ml-2">Add</button>
            </form>
        );
    }
}



export default Form;