import React from 'react';
import './App.css';
import FlipMove from 'react-flip-move';

const Tasks = (props) => {
    const list = props.items.map((item, index) => {
        return (
            <li id="li" className="list-group-item"
                key={index}> <input name="task" value={item.text} onChange={(e) => props.changeText(e.target.value, index)} /> <span
                    onClick={() => props.deleteItem(index)} id="item" className="ml-5">
                    <i className="fa fa-trash" aria-hidden="true"></i></span></li>
        );
    });

    return (
        <ul className="list-group mt-2 mb-5">
            <FlipMove duration={300} easing="ease-in-out">
                {list}
            </FlipMove>
        </ul >
    )


}

export default Tasks;
